import React, {Component} from 'react';
import PropTypes from 'prop-types';

/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */

export default class StreamingProvider extends Component {
    constructor(props) {
        super(props);
        this.state = {
            defaults: props.data,
            current: props.data,
        };

        this.connection = new WebSocket(
            'wss://ws.finnhub.io?token=' + props.label
        );

        this.connection.addEventListener('message', evt => {
            const message = JSON.parse(evt.data);
            if (message.type === 'trade') {
                const {s, p} = message.data[0];
                props.setProps({value: `${s}, ${p}`});
            }
        });

        this.connection.addEventListener('open', evt => {
            const {symbols} = JSON.parse(props.data);
            symbols.map(symbol => {
                this.sendMessage('subscribe', symbol);
            });
        });

        this.sendMessage = this.sendMessage.bind(this);
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.data !== prevState.defaults) {
            return {
                ...prevState,
                current: nextProps.data,
            };
        }
        return null;
    }

    componentDidUpdate(prevProps) {
        if (this.props.data !== prevProps.data) {
            if (this.state.current !== this.state.defaults) {
                this.sendMessage('unsubscribe', this.state.current);
            }
            this.sendMessage('subscribe', this.props.data);
        }
    }

    componentWillUnmount() {
        const {symbols} = JSON.parse(this.state.defaults);
        const unsubs = [...symbols];
        if (this.state.current && this.state.current !== this.state.defaults) {
            unsubs.push(this.state.current);
        }
        unsubs.map(symbol => {
            this.sendMessage('unsubscribe', symbol);
        });
    }

    sendMessage(action, symbol) {
        this.connection.send(JSON.stringify({type: action, symbol: symbol}));
    }

    render() {
        const {id, value} = this.props;

        return (
            <div id={id} style={{display: 'none'}}>
                {value}
            </div>
        );
    }
}

StreamingProvider.defaultProps = {};

StreamingProvider.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * An api key that will be used when this component is rendered.
     */
    label: PropTypes.string,
    data: PropTypes.string,
    /**
     * A json string used as the default input must contain a symbols key
     * ex "{"symbols": ["SPY", "QQQ", "DIA", "XIU.TO"]}".
     */
    value: PropTypes.string,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
};
